@import "https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap";
body {
  margin: auto;
  display: block;
}

header {
  background: #fff;
  justify-content: space-between;
  align-items: center;
  padding-left: 50px;
  padding-right: 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo {
  width: 40px;
  height: 40px;
  margin: 0;
  display: block;
}

.logo_image {
  background: url("logo.d777a287.png") 0 0 / contain no-repeat;
  width: 100%;
  height: 100%;
  margin: 0;
}

ul {
  margin-block: 1em;
  margin-inline-start: 0;
  padding-inline-start: 40px;
  list-style-type: disc;
  display: block;
}

.nav_list {
  text-align: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav_item {
  justify-content: space-between;
  align-items: center;
  margin-left: 20px;
  display: flex;
}

.nav_container {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  display: flex;
}

.nav_link {
  white-space: nowrap;
  text-transform: uppercase;
  color: #000;
  align-items: center;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  transition: color .3s;
  display: flex;
  position: relative;
}

.nav_link:hover {
  color: #00acdc;
}

.nav_link.is-active {
  color: #00acdc;
  text-align: center;
  justify-content: center;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav_link.is-active:after {
  content: "";
  background: #00acdc;
  border-radius: 2px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.nav_item:first-child {
  margin-left: 0;
}

.nav_item:last-child {
  margin-right: 0;
}
